import { isUndefined } from '@guardian/libs';
import { addRegionIdToSupportUrl } from '@guardian/support-dotcom-components';
const generateAbTestArray = (abTestName, abTestVariant, targetingAbTest, amountsAbTestName, amountsAbTestVariant) => {
    const abTests = [
        {
            name: abTestName,
            variant: abTestVariant,
        },
    ];
    if (targetingAbTest) {
        abTests.push({
            name: targetingAbTest.testName,
            variant: targetingAbTest.variantName,
        });
    }
    if (amountsAbTestName && amountsAbTestVariant) {
        abTests.push({
            name: amountsAbTestName,
            variant: amountsAbTestVariant,
            testType: 'AMOUNTS_TEST',
        });
    }
    return abTests;
};
const encodeAcquisitionsData = (params, abTests) => {
    return encodeURIComponent(JSON.stringify({
        source: params.platformId,
        componentId: params.campaignCode,
        componentType: params.componentType,
        campaignCode: params.campaignCode,
        abTests,
        referrerPageviewId: params.ophanPageId,
        referrerUrl: params.referrerUrl,
        isRemote: true, // Temp param to indicate served by remote service
        labels: params.labels,
    }));
};
const generateQueryString = (params, acquisitionData, numArticles) => {
    const trackingLinkParams = {
        REFPVID: params.ophanPageId || 'not_found',
        INTCMP: params.campaignCode || '',
        acquisitionData,
        numArticles: numArticles || 0,
    };
    const queryString = Object.entries(trackingLinkParams)
        .filter(([, value]) => !isUndefined(value))
        .map(([key, value]) => `${key}=${value}`);
    return queryString.join('&');
};
export const addTrackingParams = (baseUrl, params, numArticles, amountsAbTestName, amountsAbTestVariant) => {
    const abTests = generateAbTestArray(params.abTestName, params.abTestVariant, params.targetingAbTest, amountsAbTestName, amountsAbTestVariant);
    const acquisitionData = encodeAcquisitionsData(params, abTests);
    const queryString = generateQueryString(params, acquisitionData, numArticles ?? 0);
    const alreadyHasQueryString = baseUrl.includes('?');
    return `${baseUrl}${alreadyHasQueryString ? '&' : '?'}${queryString}`;
};
const addAbandonedBasketContributionParams = (baseUrl, { region, amount, billingPeriod }) => {
    return `${baseUrl}/${region}/contribute/checkout?selected-amount=${amount}&selected-contribution-type=${billingPeriod}`;
};
export const addAbandonedBasketParamsToUrl = (baseUrl, abandonedBasket) => {
    switch (abandonedBasket.product) {
        case 'Contribution':
        case 'SupporterPlus':
            return addAbandonedBasketContributionParams(baseUrl, abandonedBasket);
        default:
            return baseUrl;
    }
};
export const addAbandonedBasketAndTrackingParamsToUrl = (baseUrl, abandonedBasket, tracking) => {
    return addTrackingParams(addAbandonedBasketParamsToUrl(baseUrl, abandonedBasket), tracking);
};
export const isSupportUrl = (baseUrl) => /\bsupport\./.test(baseUrl);
export const addRegionIdAndTrackingParamsToSupportUrl = (baseUrl, tracking, numArticles, countryCode, amountsAbTestName, amountsAbTestVariant) => {
    if (!isSupportUrl(baseUrl)) {
        return baseUrl;
    }
    const urlWithRegion = addRegionIdToSupportUrl(baseUrl, countryCode);
    return addTrackingParams(urlWithRegion, tracking, numArticles, amountsAbTestName, amountsAbTestVariant);
};
const hrefRegex = /href="(.*?)"/g;
export const addTrackingParamsToBodyLinks = (text, tracking, numArticles, countryCode) => {
    const trackingWithLabel = addLabelToTracking(tracking, 'body-link');
    const replaceHref = (wholeMatch, url) => `href="${addRegionIdAndTrackingParamsToSupportUrl(url, trackingWithLabel, numArticles, countryCode)}"`;
    return text.replace(hrefRegex, replaceHref);
};
export const addProfileTrackingParams = (baseUrl, params) => {
    const constructQuery = (query) => Object.keys(query)
        .map((param) => {
        const value = query[param];
        const queryValue = Array.isArray(value)
            ? value.map((v) => encodeURIComponent(v)).join(',')
            : encodeURIComponent(value);
        return `${param}=${queryValue}`;
    })
        .join('&');
    const componentEventParamsData = {
        componentType: params.componentType,
        componentId: params.campaignCode,
        abTestName: params.abTestName,
        abTestVariant: params.abTestVariant,
        viewId: params.ophanPageId,
    };
    const trackingLinkParams = {
        // Note: profile subdomain take uri encoded query params NOT json
        componentEventParams: encodeURIComponent(constructQuery(componentEventParamsData)),
        returnUrl: params.referrerUrl,
    };
    const queryString = Object.entries(trackingLinkParams)
        .filter(([, value]) => !isUndefined(value))
        .map(([key, value]) => `${key}=${value}`);
    const alreadyHasQueryString = baseUrl.includes('?');
    return `${baseUrl}${alreadyHasQueryString ? '&' : '?'}${queryString.join('&')}`;
};
export const addChoiceCardsParams = (url, frequency, amount) => {
    const newParams = `selected-contribution-type=${frequency}${amount !== undefined ? `&selected-amount=${amount}` : ''}`;
    const alreadyHasQueryString = url.includes('?');
    return `${url}${alreadyHasQueryString ? '&' : '?'}${newParams}`;
};
export const addChoiceCardsOneTimeParams = (url) => {
    const newParams = `oneTime`;
    const alreadyHasQueryString = url.includes('?');
    return `${url}${alreadyHasQueryString ? '&' : '?'}${newParams}`;
};
export const addChoiceCardsProductParams = (url, product, ratePlan, contribution) => {
    const newParams = `product=${product}&ratePlan=${ratePlan}${contribution !== undefined ? `&contribution=${contribution}` : ''}`;
    const alreadyHasQueryString = url.includes('?');
    return `${url}${alreadyHasQueryString ? '&' : '?'}${newParams}`;
};
export const isProfileUrl = (baseUrl) => /\bprofile\./.test(baseUrl);
export const addTrackingParamsToProfileUrl = (baseUrl, tracking) => {
    return isProfileUrl(baseUrl)
        ? addProfileTrackingParams(baseUrl, tracking)
        : baseUrl;
};
// SHARED TRACKING
const campaignPrefix = 'gdnwb_copts_memco';
export const buildCampaignCode = (test, variant) => `${campaignPrefix}_${test.campaignId ?? test.name}_${variant.name}`;
export const buildBannerCampaignCode = (test, variant) => `${test.name}_${variant.name}`;
export const buildAmpEpicCampaignCode = (testName, variantName) => `AMP__${testName}__${variantName}`;
const createEventFromTracking = (action) => {
    return (tracking, componentId) => {
        const { abTestName, abTestVariant, componentType, products = [], labels = [], campaignCode, } = tracking;
        const abTest = abTestName && abTestVariant
            ? {
                name: abTestName,
                variant: abTestVariant,
            }
            : null;
        const targetingAbTest = tracking.targetingAbTest
            ? {
                name: tracking.targetingAbTest.testName,
                variant: tracking.targetingAbTest.variantName,
            }
            : null;
        return {
            component: {
                componentType,
                products,
                campaignCode,
                id: componentId,
                labels,
            },
            ...(abTest ? { abTest } : {}),
            ...(targetingAbTest ? { targetingAbTest } : {}),
            action,
        };
    };
};
export const addLabelToTracking = (tracking, label) => {
    if (tracking.labels) {
        return {
            ...tracking,
            labels: [...tracking.labels, label],
        };
    }
    else {
        return {
            ...tracking,
            labels: [label],
        };
    }
};
export const createClickEventFromTracking = createEventFromTracking('CLICK');
export const createViewEventFromTracking = createEventFromTracking('VIEW');
export const createInsertEventFromTracking = createEventFromTracking('INSERT');
